.block-config-mail-box {
  display: none;
}

.block-config-mail-box .block__image {
  margin-bottom: 35px;
}

.block-config-mail-box .block__content {
  max-width: 600px;
  text-align: center;
}

@media only screen and (min-width: 768px) {
  .block-config-mail-box {
    display: flex;
    flex-flow: column;
    align-items: center;
  }

  .block-config-mail-box .block__content p {
    font-weight: 500;
    font-size: 15px;
    line-height: 20px;
  }
}

@media only screen and (min-width: 1024px) {
  .block-config-mail-box .block__content p {
    font-size: 20px;
    line-height: 28px;
  }
}